import Vue from 'vue'
import Vuex from 'vuex'

// Vuex
Vue.use(Vuex)

const actions = {}

const mutations = {
    // 
    LOG_OUT(state)
    {
        sessionStorage.clear()
        state.username = ''
        state.marketId = ''
        state.role = ''
        state.badgeHide = true
        state.marketTotalNumber = 0
    },
    //
    ADD_MARKET_TOTAL_NUMBER(state,value)
    {
        state.marketTotalNumber += value
    },
    SUB_MARKET_TOTAL_NUMBER(state,value)
    {
        state.marketTotalNumber -= value
    }
}

const state = {
    username: '',
    marketId: '',
    role: '',
    // 
    badgeHide: true,
    marketTotalNumber: 0
}


// Vuex
export default new Vuex.Store({
    actions,
    mutations,
    state
})