//
function fillZero(str) {
  var realNum;
  if (str < 10) {
      realNum = '0' + str;
  } else {
      realNum = str;
  }
  return realNum;
}

Vue.prototype.$getCurrentDate = function getTime() {
  var myDate = new Date();
  var myYear = myDate.getFullYear(); //(4,1970-????)
  var myMonth = myDate.getMonth() + 1; //(0-11,01)
  var myToday = myDate.getDate(); //(1-31)
  var myHour = myDate.getHours(); //(0-23)
  var myMinute = myDate.getMinutes(); //(0-59)
  var mySecond = myDate.getSeconds(); //(0-59)
  return myYear + '-' + fillZero(myMonth) + '-' + fillZero(myToday) + ' ' + fillZero(myHour) + ':' +
      fillZero(myMinute) + ':' + fillZero(mySecond);
}

new Vue({
  render: h => h(App),
  store,
  router,
}).$mount('#app')
